* {
    box-sizing: border-box;
}

body {
    font-size: 13px;
    font-family: "MyriadArabic";
    margin: 0;
    overflow-x: hidden;
    direction: ltr;
}

.badge {
    font-size: 18px;
    background-color: #E52B52;
}

del {
    font-size: 18px;
    color: #E52B52;
    font-weight: bold;
    margin-left: 10px;
}

a {
    text-decoration: none !important;
}

.flex-center {
    display: flex;
    align-items: center;
}

@media screen and (max-width:767px) {
    .col-xs-hide {
        display: none;
    }
}

.glyphicon {
    position: relative;
    top: 3px;
}

.form-control:focus {
    border-color: #ff8ca4;
    box-shadow: inset 0 1px 1px rgba(0, 0, 5, .075), 0 0 8px #ff8ca4;
}

.option {
    display: flex;
    align-items: center;
    justify-content: center;
    // border-bottom: 1px solid #eee;
    padding-bottom: 20px;
    flex-wrap: wrap;
    // margin-top: 20px;

    @media screen and (max-width:900px) {
        display: none;
    }

    .option__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 10px;

        img {
            width: 150px;
        }

        div {
            color: #111;
            font-weight: 600;
        }
    }
}

.breadcrumb {
    direction: rtl;
    margin-top: 20px;


    &>li {
        font-weight: 600;
        font-size: 18px;
    }
}

.btn-danger {
    background-color: #E52B52;
}

.running {
    padding: 0 10px;
    font-size: 17px;
    color: #111;
    font-weight: 600;
}

.bg-header {
    min-height: 250px;
    width: 100%;
    background-image: url('../img/bg-header.png');
    margin-top: 20px;
    background-size: cover;
    position: relative;
    padding: 30px 10px;
    border-top: 3px solid #E52B52;
    border-bottom: 3px solid #E52B52;
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    &::before {
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        right: 0;
        z-index: 1;
        background: #0b1520;
        opacity: 0.7;
    }

    .title {
        font-size: 30px;
        font-weight: 600;
        text-align: center;        
        position: relative;
        color: #fff;
        z-index: 1;
        margin:0;
        text-shadow: 1px 5px 4px #E52B52;
    }

    .desc {
        font-size: 20px;
        font-weight: 600;
        text-align: center;
        position: relative;
        color: #fff;
        z-index: 1;
        text-shadow: 1px 5px 4px #E52B52;
        margin-top: 15px;
    }
}