.category {
    box-shadow: 1px 1px 10px #ccc;
    border: 1px solid #ccc;
    overflow: hidden;

    form {
        padding: 10px;
        direction: rtl;
    }

    .col-md-3.col-sm-6.col-xs-12 {
        padding: 0;
        margin: 0;
    }

    .category__item {
        // border-radius: 5px;
        display: block;
        transition: all 0.4s;
        border: 1px solid #ddd;

        &:hover {
            // border: 1px solid #eee;
            box-shadow: 0px 0px 10px #ccc;
        }

        .category__item__title {
            font-size: 18px;
            text-align: center;
            font-weight: 600;
            color: #4a5f73;
            margin-top: 15px;
        }

        .category__item__img {
            text-align: center;
            height: 200px;

            img {
                max-height: 200px;
                margin: auto;
            }
        }


        .category__item__price {
            font-size: 18px;
            font-weight: 600;
            padding: 0 10px;
            color: #111;
        }

        .category__item__btn {
            padding: 10px;
        }
    }
}

.page {
    text-align: center;

    .pagination>.active>a,
    .pagination>.active>a:focus,
    .pagination>.active>a:hover,
    .pagination>.active>span,
    .pagination>.active>span:focus,
    .pagination>.active>span:hover {
        background-color: #E52B52;
        border-color: #E52B52;
    }

    .pagination {
        margin: 20px 0 0;
    }
}

@media screen and (max-width:991px) {
    .control-group {
        margin-bottom: 10px;
    }
}