.cart {
    box-shadow: 0px 0px 10px #ccc;
    border-radius: 5px;
    background-color: #fff;
    border: 1px solid #eee;
    padding: 10px;  
    overflow: hidden;
    
    .cart__title {
        text-align: right;
        font-size: 18px;
        font-weight: 600;
        border-bottom: 1px solid #ccc;
        padding-bottom: 10px;
        margin-bottom: 15px;
    }

    .cart__price {
        display:flex;
        align-items: center;
        justify-content: space-between;
        font-size: 20px;
        font-weight: 600;
        border-bottom: 1px solid #ccc;
        padding-bottom: 10px;
        direction: rtl;
        margin-bottom: 15px;
    }
}

.table {
    direction: rtl;

    tbody>tr>td>img {
        max-width: 80px;
    }
}

.table>tbody>tr>td, .table>tbody>tr>th, .table>tfoot>tr>td, .table>tfoot>tr>th, .table>thead>tr>td, .table>thead>tr>th {
    vertical-align: middle;
    text-align: center;
    margin:auto;
    font-size: 16px;
}

.input-group {
    direction: rtl;
}