.carousel {
    margin-top: 20px;
    direction: ltr;
    border-radius: 10px;
    overflow: hidden;

    @media screen and (max-width:767px) {
        border-radius: 0;
        margin-top: 0;
    }

    .item {
        background-color: #777;
        border-radius: 10px;

        @media screen and (max-width:767px) {
            border-radius: 0;
        }
    }
}