.header {
    padding: 10px;
    background-color: #fff;
    box-shadow: 0px 10px 10px #ccc;
    direction: rtl;

    @media screen and (max-width: 767px) {
        padding: 10px 0;
    }

    .header__btn-cart {
        // font-size: 18px;
        // font-weight: 500;

        i.fa {
            margin-left: 10px;
            position: relative;
            top: 2px;
        }

        span {
            margin-right: 10px;
        }
    }
}

.navbar-inverse {
    background-color: #fff;
    border-color: #fff;
    border-radius: 0px !important;
    border-bottom: 1px solid #eee;
    direction: rtl;
    margin-bottom: 0px;
}

.navbar-inverse .navbar-nav>li>a {
    font-size: 15px;
    font-weight: 600;
    color: #111;
}

.navbar-inverse .navbar-nav>li>a:focus,
.navbar-inverse .navbar-nav>li>a:hover {
    color: #E52B52;
}

.navbar-inverse .navbar-nav>.active>a,
.navbar-inverse .navbar-nav>.active>a:focus,
.navbar-inverse .navbar-nav>.active>a:hover {
    background-color: #eee;
    color: #E52B52;
}

.navbar-toggle {
    background-color: #333;
}

.navbar-brand {
    color: #333 !important;
    padding: 5px 10px;
    height: auto;
    font-size: 16px;
    line-height: 20px;
    margin-top: 9px;
    margin-right: 15px;
    display: none;

    @media screen and (max-width: 767px) {
        display: block;
    }
}

.btn.active.focus,
.btn.active:focus,
.btn.focus,
.btn:active.focus,
.btn:active:focus,
.btn:focus {
    outline: none !important;
    outline-offset: 0px !important
}

.btn {
    padding: 5px 12px;
}


.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 10000;
    top: 0;
    right: 0;
    background-color: #fff;
    overflow-x: hidden;
    transition: 0.5s;
    padding: 10px 0;
    box-shadow: 0px 10px 10px #ccc;
    direction: rtl;

    .closebtn {
        position: absolute;
        top: 0;
        left: 25px;
        font-size: 36px;
    }

    .sidenav__content {
        padding: 10px;

        .sidenav__content__title {
            color: #111;
            text-align: center;
            font-weight: 600;
            font-size: 18px;
        }

        .sidenav__content__img {
            border-bottom: 1px solid #ccc;
            display: block;
            width: 100%;
            padding-bottom: 10px;
            margin-bottom: 10px;

            img {
                margin: auto;
            }
        }
    }
}

.tree {
    margin-top: 10px;

    ul,
    #myUL {
        list-style-type: none;
    }

    a {
        font-size: 14px;
        font-weight: 600;
        color: #111;
    }

    #myUL {
        margin: 0;
        padding: 0;

        li {
            margin-right: 10px;
        }
    }

    .parent {
        cursor: pointer;
        font-size: 14px;
        font-weight: 600;
    }

    .parent::before {
        content: "\f055";
        color: black;
        display: inline-block;
        margin-left: 6px;
        font-family: FontAwesome;
        position: relative;
        top: 2px;
    }

    .parent-down::before {
        content: "\f056";
        color: black;
        display: inline-block;
        margin-left: 6px;
        font-family: FontAwesome;
        position: relative;
        top: 2px;
    }

    .nested {
        display: none;
    }

    .active {
        display: block;
    }
}