.admin {
  .admin__header {
    background-color: #fff;
    top: 0;
    right: 0;
    position: fixed;
    z-index: 1000;
    width: 100%;
    height: 60px;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    direction: rtl;
    box-shadow: 0px 2px 5px #999;
    border-bottom: 1px solid #ccc;

    .dropdown-toggle {
      color: #000;
      cursor: pointer;
      font-size: 15px;
      font-weight: 600;
    }

    .dropdown-menu {
      // right: -38px;
      padding: 0px;
      a {
        color: #000;
        width: 100%;
        font-size: 15px;
        font-weight: 600;
        border-top: 1px solid #ccc;
      }
    }

    a {
      text-decoration: none;
      color: #000;
      width: 100%;
      font-size: 15px;
      font-weight: 600;
    }
  }

  .sidebar {
    margin: 0;
    padding: 0;
    width: 230px;
    background-color: #fff;
    position: fixed;
    height: calc(100% - 60px) !important;
    overflow: auto;
    top: 60px;
    right: 0;
    direction: rtl;
    box-shadow: 0px 5px 5px #999;
    border-top: 1px solid #ccc;

    @media (max-width: 767px) {
      &.in {
        display: block;
        position: static;
        height: auto;
        width: 100%;
        margin-top: 60px;
        margin-bottom: -60px;
      }
    }

    @media (min-width: 767px) {
      &.collapse {
        display: block;
      }
    }

    .sidebar__profile {
      padding: 10px;
      background-color: #eeeeee75;

      .sidebar__profile__img {
        text-align: center;

        img {
          max-width: 100px;
          border: 2px solid #337ab7;
          margin: auto;
        }
      }

      .sidebar__profile__title {
        color: #000;
        font-size: 18px;
        font-weight: 600;
        text-align: center;
        margin-top: 10px;
      }
    }
  }

  .sidebar a {
    display: block;
    color: #337ab7;
    font-weight: 600;
    padding: 14px;
    text-decoration: none;
    font-size: 15px;
    border-top: 1px solid #ccc;
  }

  .sidebar a.active {
    background-color: #08467a;
    color: white;
  }

  .sidebar a:hover:not(.active) {
    background-color: #eeeeee75;
  }

  .content {
    margin-right: 230px;
    padding: 20px;
    margin-top: 60px;
  }

  .admin__box {
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0px 0px 10px #ccc;
    background-color: #e52b52;
    border-radius: 5px;
    margin-bottom: 15px;
    direction: rtl;
    padding: 10px;
    color: #fff;

    .amin__box__title {
      font-size: 18px;
      font-weight: 600;
      margin-right: 10px;
    }

    .amin__box__icon {
      font-size: 40px;
      font-weight: 600;
    }
  }

  .admin__panel {
    box-shadow: 0px 0px 10px #ccc;
    border: 1px solid #ccc;
    border-top: 3px solid #e52b52;
    direction: rtl;
    margin: 0 0 20px;
    border-radius: 5px;

    .admin__panel__title {
      display: flex;
      align-items: center;
      background-color: #eee;
      font-weight: 600;
      font-size: 18px;
      padding: 10px;

      i {
        font-size: 40px;
        margin-left: 15px;
      }
    }

    .admin__panel__link {
      display: block;
      color: #337ab7;
      font-weight: 600;
      padding: 10px;
      text-decoration: none;
      font-size: 15px;
      border-top: 1px solid #ccc;

      &:hover {
        background-color: #eeeeee75;
      }
    }
  }

  .admin__box-primary {
    background-color: #337ab7 !important;
  }

  .admin__box-success {
    background-color: #28a745 !important;
  }

  .admin__box-warning {
    background-color: #ffc107 !important;
  }

  .breadcrumb {
    margin-top: 0 !important;
  }

  // @media screen and (max-width: 400px) {
  //   .sidebar a {
  //     text-align: center;
  //     float: none;
  //   }
  // }

  .panel .panel-primary > .panel-heading {
    font-weight: 600;
    font-size: 20px;
    background-color: #00bfff !important;
  }

  .breadcrumb a {
    font-size: 16px;
    font-weight: 600;
  }

  .navbar-toggle {
    background-color: transparent;
    border: 1px solid #000;

    .icon-bar {
      background-color: #000;
    }
  }

  .collapse-toggle {
    display: none;
  }

  @media screen and (max-width: 767px) {
    .sidebar {
      display: none;

      .sidebar__profile {
        display: none;
      }
    }

    .collapse-toggle {
      display: block;
    }

    .content {
      margin-right: 0px;
    }

    & > .admin__header > .dropdown {
      display: none;
    }
  }
}

.panel-primary > .panel-heading {
  direction: rtl;
  font-size: 18px;
  font-weight: 600;
}

.notification {
  i.fa {
    font-size: 28px;
    position: relative;
  }

  .badge {
    position: absolute;
    top: -5px;
    right: -15px;
    z-index: 10;
  }
}
