.scroll-top {
    background-color: #5E6B7B;
    margin-top: 30px;
    padding: 10px;
    text-align: center;
    color: #fff;
    font-size: 20px;
    cursor: pointer;

    i.fa {
        margin: 0 5px;
        position: relative;
        top: 2px;
    }
}

.footer-bottom {
    background-color: #5E6B7B;
    padding: 10px;
    text-align: center;
    color: #fff;
    font-size: 14px;

    a {
        color: #ff8ca4;
    }
}

.footer {
    padding: 20px 0;
    background-color: #232F3E;

    .footer__option {
        display: flex;
        align-items: center;
        justify-content: center;
        // border-bottom: 1px solid #eee;
        padding-bottom: 20px;
        flex-wrap: wrap;

        .footer__option__item {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin: 10px;

            img {
                width: 100px;
            }

            div {
                color: #fff;
                font-weight: 600;
            }
        }
    }

    .footer__info {
        margin-top: 20px;
        color: #fff;

        .footer__info__title {
            color: #fff;
            font-weight: 600;
            font-size: 18px;
            padding-bottom: 10px;
            border-bottom: 1px dotted #ccc;
            text-align: right;

            @media screen and (max-width:767px) {
                margin-top: 15px;
            }
        }

        .footer__info__link,
        p {
            color: #fff;
            display: block;
            text-align: right;
            margin: 10px 0;
            font-size: 16px;
        }

        .footer__info__link:hover {
            color: #E52B52;
        }

        .footer__info__share {
            width: 30px;
            margin-left: 10px;
        }
    }
}