.offer {
    box-shadow: 0px 0px 10px #ccc;
    border-radius: 10px;
    border: 1px solid #eee;
    margin: 20px 0 0;
    padding: 30px;
    position: relative;

    @media screen and (max-width: 767px) {
        border-radius: 0px;
    }

    .offer__arrow {

        .offer__arrow__left,
        .offer__arrow__right {
            position: absolute;
            box-shadow: 0px 0px 3px #ccc;
            border: 1px solid #ccc;
            top: 45%;
            height: 60px;
            width: 30px;
            font-size: 20px;
            font-weight: 600;
            color: #ccc;
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            z-index: 100;
            background-color: #ffffff;
        }

        .offer__arrow__right {
            right: 1px;
            border-radius: 5px 0px 0px 5px;
            border-right: 0px;
        }

        .offer__arrow__left {
            left: 1px;
            border-radius: 0px 5px 5px 0px;
            border-left: 0px;
        }
    }

    .offer__product {
        direction: ltr;
        margin-top: 15px;

        .offer__product__item {
            margin: 5px;
            border-radius: 5px;
            display: block;
            transition: all 0.4s;

            // &:hover {
            //     // border: 1px solid #eee;
            //     box-shadow: 0px 0px 10px #ccc;
            // }

            .offer__product__item__title {
                font-size: 18px;
                text-align: center;
                font-weight: 600;
                color: #4a5f73;
                margin-top: 15px;
            }

            .offer__product__item__img {
                text-align: center;
                // height: 200px;

                img {
                    // max-height: 200px;
                    margin: auto;
                }
            }


            .offer__product__item__price {
                font-size: 18px;
                font-weight: 600;
                padding: 0 10px;
                color: #111;
            }

            .offer__product__item__btn {
                padding: 10px;
            }
        }
    }

    .offer__title {
        font-size: 20px;
        color: #222;
        font-weight: 600;
        text-align: right;
        padding-bottom: 10px;
        border-bottom: 1px dotted #ccc;
        position: relative;

        &::before {
            position: absolute;
            top: 37px;
            right: 0px;
            content: '';
            width: 150px;
            height: 2px;
            background-color: #E52B52;
        }

        a {
            float: left;
            font-size: 14px;

            i {
                font-size: 12px;
                margin-left: 5px;
                position: relative;
                top: 2px;
            }
        }
    }
}