.detail {
    box-shadow: 1px 1px 10px #ccc;
    border: 1px solid #cccccc;
    overflow: hidden;
    padding: 15px;
    border-radius: 10px;

    .detail__title {
        text-align: right;
        font-weight: 600;
    }

    .detail__img {
        text-align: center;

        img {
            margin: auto;
            cursor: pointer;
        }
    }

    .detail__img-detail {

        margin-top: 20px;

        .col-xs-3 {
            padding: 5px;
        }

        img {
            cursor: pointer;
        }
    }

    .detail__desc {
        font-size: 18px;
        color: #111;
        font-weight: 600;
        text-align: right;
        padding: 5px 10px;
        border-bottom: 1px solid #ccc;
    }

    .detail__price {
        font-size: 25px;
        text-align: left;
        color: #111;
        font-weight: 600;
        padding: 5px 10px;
    }

    .detail__feature {

        .detail__feature__title {
            font-size: 16px;
            font-weight: 600;
            text-align: right;
            border-bottom: 1px solid #ccc;
            padding-bottom: 10px;
            margin-top: 20px;
        }

        .detail__feature__list {
            padding: 0;
            margin: 0;
            direction: rtl;
            list-style: none;

            li {
                font-size: 16px;
                padding: 5px 15px;
                position: relative;

                &::before {
                    content: '';
                    position: absolute;
                    right: 0px;
                    top: 12px;
                    width: 8px;
                    height: 8px;
                    background-color: #E52B52;
                    border-radius: 50%;
                }
            }
        }
    }
}

.form-group {
    direction: rtl;

    label {
        font-size: 14px;
    }
}

.detail-tab {
    box-shadow: 1px 1px 10px #ccc;
    border: 1px solid #cccccc;
    overflow: hidden;
    padding: 15px;
    border-radius: 10px;
    margin-top: 20px;

    .tab-content>.active {
        direction: rtl;
        font-size: 16px;
        line-height: 30px;
        text-align: justify;
        margin-top: 20px;
    }

    .nav-tabs>li>a {
        font-size: 16px;
        font-weight: 600;
    }

    .detail-tap__comment {
        display: flex;
        align-items: center;
        margin-top: 15px;
        border-radius: 10px;
        border: 1px solid #ccc;
        padding:5px;

        img {
            width: 80px;
        }

        p {
            margin:0px;
            font-size: 16px;
            font-weight: 600;
        }
    }


}